*
{
  margin: 0;
  padding: 0;
}

body{
  background: url(https://images.rawpixel.com/image_800/cHJpdmF0ZS9sci9pbWFnZXMvd2Vic2l0ZS8yMDIyLTA1L3JtNDIyLTA3Ni14LmpwZw.jpg);
  font-family: "bree serif",serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
}
.heading{
  text-align: center;
  color: rgba(233, 178, 12, 0.978);
}
.app{
  background: linear-gradient(83deg,gold 0%,blue 80%);
  width: 95%;
  min-height: 200px;
  height: min-content;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0,0,0, 0.75);
  display: flex;
  justify-content: space-evenly;

}
.Question-section{
  position: relative;
  width: 100%;
}
.Question-count
{
  color: rgb(29, 29, 69);
  font-size: 30px;
  margin-bottom: 20px;

}
.Question-text{
  font-size: 30px;
  color: blanchedalmond;
}
.Answer-section{
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: space-between;
}
button{
  width: 100%;
  font-size: 16px;
  color: wheat;
  background: linear-gradient(83deg,rgb(192, 171, 53),rgba(123, 102, 102, 0.122));
  border-radius: 5px;
  display: flex;
  padding: 10px;
  justify-content: flex-start;
  align-items: center;
  border: 3px solid gold;
  cursor: pointer;
}
.score-section{
  text-align: center;
  color: white;
  font-size: 25px;

}